@import "variables.scss";
.design__picture > img {
  border-radius: 12px;
}

.design__small {
  display : block;
  font-weight: 300;
  font-size: 14px;
  line-height: 21px;
  color: #828282;
}

.design__span {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #333333;
}

.design__heading {
  font-weight: 500;
  font-size: 36px;
  line-height: 54px;
  color: #333333;
  max-width: 448px;
}

@media (min-width : $min-width) {
  .design__pictures {
    display : flex;
    flex-direction : row;
    flex-wrap : wrap;
    justify-content : space-between;
    row-gap: 10vh;
  }

  .design__picture:nth-of-type(1) {
    padding-top: 15%;
    flex-basis : 48%;
  }
  .design__picture:nth-of-type(2) {
    padding-bottom: 15%;
    flex-basis : 48%;
  }
  .design__picture:nth-of-type(3) {
    padding-top: 15%;
    flex-basis : 48%;
  }
  .design__picture:nth-of-type(4) {
    padding-bottom: 15%;
    flex-basis : 48%;
  }
}